import {
  Card,
  CardContent,
  Typography,
  Box,
  IconButton,
  Button,
} from "@mui/material";
import IosShareIcon from "@mui/icons-material/IosShare";
import HighlightAltIcon from "@mui/icons-material/HighlightAlt";
import MyLocationIcon from "@mui/icons-material/MyLocation";
import { Link } from "react-router-dom";
import { useState } from "react";
import { URL } from "../../URL";
import CustomBTN from "../CustomButton/CustomBTN";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import LikeView from "./LikeView";
import axios from "axios";
import Loading from "../../Loading/Loading";
import { toast } from "react-toastify";
import { AddCeil, AddfailNfts } from "../../Redux/Reducer";
import { useDispatch, useSelector } from "react-redux";
import bloctech from "../../images/bloctech.jpg";
function MarketplaceCard(props) {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const handleOpen = (_id) => {
    setOpen(true);
  };
  const { ceilSlice } = useSelector((state) => state);
  const { userdetails } = ceilSlice;
  const filterRole =
    userdetails.role && userdetails?.role.filter((role) => role !== null);
  const videoType = ["mov", "mp4", "avi", "wmf", "flv", "webm"];
  const {
    address,
    locations,
    id,
    name,
    filelink,
    Image,
    file,
    user,
    productTotalPrice,
    userlikeviews,
    howManyViews,
    paidStatusContract,
    failedNftmint,
  } = props;
  const result = file?.split(".");
  let fileType = result?.pop();
  const mintFaildNFT = async (_id, address) => {
    try {
      setLoading(true);
      let mintNft = { _id, address };
      const { data } = await axios.post(`${URL}/mintfailedNft`, mintNft);
      if (data?.data === true) {
        dispatch(AddCeil(data?.nftes?.nfts));
        dispatch(AddfailNfts(data?.nftes?.failedNfts));
        toast.success(data?.message);
        failedNftmint(true);
      }
      data?.data === false && toast.error(data?.message);
      setLoading(false);
    } catch (error) {
      console.log("Nft mint failed error ");
      setLoading(false);
    }
  };
  return (
    <>
      {loading && <Loading loading={Loading} />}
      <Card
        sx={{
          backgroundColor: "background.secondary",
          minHeight: "440px",
          maxHeight:"475px",
          borderRadius: "15px",
        }}
      >
        <Box sx={{ position: "relative", minHeight: "250px" }}>
          {/* <Box
            sx={{
              position: "absolute",
              top: "10px",
              left: "13px",
              display: "flex",
            }}
          >
            <Typography
              sx={{
                fontSize: "20px",
                backgroundColor: "#3ACAB8",
                opacity: "",
                paddingX: "8px",
                paddingY: "5px",
                borderRadius: "10px",
                color: "white",
                textDecoration: "none",
              }}
            >
              {" "}
              nft
            </Typography>
          </Box> */}
          {/* <Box
            sx={{ position: "absolute", top: "10px", zIndex: 1, right: "10px" }}
          >
            <HighlightAltIcon
              sx={{
                fontSize: "35px",
                color: "white",
                backgroundColor: "#3ACAB8",
                opacity: "",
                padding: "8px",
                borderRadius: "10px",
              }}
            />
          </Box> */}
          {/* <Box
            sx={{
              position: "absolute",
              bottom: "10px",
              zIndex: 1,
              left: "10px",
              cursor: "pointer",
            }}
          >
            <Link
              to={`/Bmap/${locations?.lang}/${locations?.long}`}
              style={{ color: "white" }}
            >
              <MyLocationIcon
                sx={{
                  fontSize: "35px",
                  backgroundColor: "#3ACAB8",
                  opacity: "",
                  padding: "8px",
                  borderRadius: "10px",
                }}
              />
            </Link>
          </Box> */}
          {/* <Box
            sx={{ position: "absolute", bottom: "10px", zIndex: 1, right: 0 }}
          >
            <IconButton
              color="white"
              onClick={() => {
                handleOpen(id);
              }}
            >
              <IosShareIcon
                sx={{
                  fontSize: "35px",
                  color: "white",
                  backgroundColor: "#3ACAB8",
                  opacity: "",
                  padding: "8px",
                  borderRadius: "10px",
                }}
              />
            </IconButton>
          </Box> */}
          {!videoType?.includes(fileType) ? (
            <Box
              sx={(theme) => ({
                boxShadow:
                  theme.palette.mode === "light"
                    ? "rgb(242,231,231) 10px 10px 20px 22px inset, rgb(242,231,231) 10px 18px 36px 28px inset"
                    : "rgb(21 0 47 / 60%) 10px 10px 20px 22px inset, rgb(21 0 47 / 60%) 10px 18px 36px 28px inset",
                backgroundImage:
                  file || filelink
                    ? `url(${URL}/upload/${file})` || `url(${filelink})`
                    : Image
                    ? `url(${URL}/upload/${Image})`
                    : `url(${bloctech})`,

                backgroundPosition: "center",
                backgroundSize: "cover",
                height: "250px",
                width: "100%",
              })}
            ></Box>
          ) : (
            <>
              {videoType?.includes(fileType) ? (
                <Box
                  sx={{
                    borderRadius: "10px 10px 0px 0px",
                    overflow: "hidden",
                    width: "100%",
                    height: "250px",
                  }}
                >
                  <video
                    src={`${URL}/upload/${file}`}
                    width="100%"
                    margin="0px"
                    controls
                    autoPlay={false}
                    style={{ minHeight: "260px" }}
                  />
                </Box>
              ) : null}
            </>
          )}
        </Box>
        <CardContent sx={{ m: { xs: 0 } }}>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography gutterBottom sx={{ pb: 1 , maxWidth:"250px", overfolow:"hidden", fontSize:"15px", fontWeight:"bold" }}  component="h2">
              {name}
            </Typography>
            <LikeView userlikeviews={userlikeviews} howManyViews={howManyViews} />
            {/* {paidStatusContract === false && filterRole?.includes("admin") ? (
              <Button
                onClick={() => {
                  mintFaildNFT(id, address);
                }}
                sx={{
                  fontSize: "12px",
                  borderRadius: "10px",
                  pb: 1,
                  background:
                    "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                  color: "white",
                }}
              >
                Mint again
              </Button>
            ) : (
              ""
            )} */}
          </Box>

          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography sx={{ fontSize: "15px" }}> Price</Typography>
            <Typography  sx={{ fontSize: "15px", backgroundColor:"background.buttonBg", px:1, py:'5px', borderRadius:"7px" }}>
              {productTotalPrice ? + "" + productTotalPrice + " ETH" : "0"}
            </Typography>
          </Box>

          {/* <LikeView userlikeviews={userlikeviews} howManyViews={howManyViews} /> */}
          {/* <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography
              sx={{ fontSize: "1rem", color: "gray", fontWeight: 700 }}
            >
              user
            </Typography>

            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                color: "gray",
              }}
            >
              <Typography
                component="p"
                sx={{
                  fontSize: "10px",
                  mr: 1,
                  maxWidth: "120px",
                  overflow: "hidden",
                }}
              >
                {user?.username ? user?.username : "User name"}
              </Typography>
              {user?.profileImage ? (
                <Box>
                  <img
                    src={`${URL}/upload/${user?.profileImage}`}
                    alt=""
                    style={{
                      width: "35px",
                      height: "35px",
                      borderRadius: "50%",
                    }}
                  />
                </Box>
              ) : (
                <Box>
                  <AccountCircleIcon
                    style={{
                      width: "35px",
                      height: "35px",
                      borderRadius: "50%",
                    }}
                  />
                </Box>
              )}
            </Box>
          </Box> */}
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "centers",
            }}
          >
            <Typography
              component="p"
              sx={{ fontSize: "1rem", color: "gray", fontWeight: 700 }}
            >
              status
            </Typography>
            <Box>
              <Typography
                color={paidStatusContract ? "green" : "red"}
                component="p"
                sx={{
                  fontSize: "12px",
                  ml: 1,
                  mt: 1,
                  fontWeight: 700,
                  textAlign: "right",
                }}
              >
                {paidStatusContract ? "Mint succes" : "pending"}
              </Typography>
            </Box>
          </Box>

          <Link
            to={`/nftDetail/${id}`}
            style={{ textDecoration: "none",  }}
          >
            <Box sx={{width:"100%",  display:"flex", alignItems:'center'}}>

             <Button
                      sx={{
                        clipPath:
                          "polygon(5% 0, 100% 0, 100% 90%, 95% 100%, 0 100%, 0 15%)",
                        background:
                          "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                        fontFamily: "'Poppins'",
                        fontWeight: 700,
                        fontSize: "12px",
                        py: 2,
                        px: 3,
                        mt: 2,
                        color: "black",
                        width:"70%",
                       
                        mx:"auto"
                      }}
                    >
                      View Details
                    </Button>
            </Box>
            {/* <CustomBTN text="view Detail" /> */}
          </Link>
        </CardContent>
      </Card>
    </>
  );
}
export default MarketplaceCard;
